<template>
  <h1 style="text-align: center;">BLOG</h1>
  <section class="blog">
    <div v-for="post in posts" :key="post.title" class="blog-card">
      <img :src="'postsFiles/' + post.imageThumb" alt="">
      <h2>{{ post.title }}</h2>
      <span>Por: {{ post.autor }}</span>
      <br>
      <br>
      <button @click="openPost(post)">Ler mais</button>
    </div>
  </section>
  <OpenPostComponent :postInfo="post" @close="this.post = null"/>
</template>

<script>
import axios from "axios";
import OpenPostComponent from "@/components/OpenPostComponent.vue";

export default {
  name: 'ListPostsComponent',
  props: {
    postsToList: {
      type: Number
    }
  },
  components: {
    OpenPostComponent
  },
  data() {
    return {
      posts: [],
      post: null
    };
  },
  mounted() {
    this.getPosts()
  },
  methods: {
    getPosts() {
      axios.get('https://soluciona.inf.br/api/solucionaGetPosts.php').then(response => {
        this.posts = response.data.slice(0, this.postsToList)
      })
    },
    openPost(post) {
      this.post = post
    }
  }
}
</script>

