<template>
  <dialog class="modal" ref="modal" @close="closePost">
    <i class="bi bi-x-circle" @click="closePost"></i>
    <section class="header-content">
      <img :src="'postsFiles/' + this.post.imageThumb" alt="">
      <div>
        <h1>{{ this.post.title }}</h1>
        <span>Por: {{ this.post.autor }}</span><br>
        <span><b>Soluciona Assessoria</b></span>
      </div>
    </section>
    <p class="info">Aperte 'Esc' para fechar</p>
    <section class="content" v-html="this.post.content">
    </section>
  </dialog>
</template>
<script>

export default {
  name: 'OpenPostComponent',
  props: {
    postInfo: {
      type: Object
    }
  },
  data() {
    return {
      post: {},
    };
  },
  watch: {
    postInfo(post) {
      if (post !== null) {
        this.post = post
        this.$refs.modal.showModal()
      }
    }
  },
  methods: {
    closePost() {
      this.$refs.modal.close()
      this.post = {}
      this.$emit('close')
    }
  }
}
</script>

